                                        <a href="" class="text-dark-lt"></a>
                                    </p>
                                    <p class="text-ellipsis-3">{{item.description}}</p>
                                    <div class="pos-abt" style="bottom:15px;">
                                        <p class="text-muted pull-left m-r">
                                            <i class="el-icon-time fa fa-clock-o m-r-xs"></i>
                                            <span>2017-10-10</span>
                                        </p>
                                        <p class="text-muted pull-left">
                                            <i class="el-icon-edit fa fa-edit m-r-xs"></i>
                                            <span>158</span>
                                        </p>
                                    </div>
                                </el-col>
                            </div>
                        </el-row>
                        <!---->
                        <el-row :gutter="10" style="margin-bottom: 50px;">
                            <el-col :lg="8" :md="8" :sm="24" :xs="24"  :offset="8">
                                <div class="block">
                                    <el-pagination layout="prev, pager, next" :total="1000">
                                    </el-pagination>
                                </div>
                            </el-col>
                        </el-row>
                    </el-col>
                    <!---->
                    <el-row :gutter="10" style="display: none" v-show="show_read">
                        <el-col :lg="16" :md="16" :sm="24" :xs="24">
                            <div class="text-center">
                                <p class="text-xxxl">2017</p>
                                <p><span>2017-10-11</span></p>
                            </div>
                            <div class="wrapper-lg">
                                <p></p>
                                <p style="text-align:left;"></p>
                                <p style="text-align:left;"> 2017</p>
                                <p style="text-align:left;"> </p>
                                <p style="text-align:left;"> 1.</p>
                                <p style="text-align:left;"> 2.</p>
                                <p style="text-align:left;"> 3.500</p>
                                <p style="text-align:left;"> 4.20</p>
                                <p style="text-align:left;"> 5</p>
                                <p style="text-align:left;"> </p>
                                <p style="text-align:left;"> 15&lt;&gt;2015297</p>
                                <p style="text-align:left;"> 22</p>
                                <p style="text-align:left;"> 31</p>
                                <p style="text-align:left;"> </p>
                                <p style="text-align:left;"> 1.1030</p>
                                <p style="text-align:left;"> 2. </p>
                                <p style="text-align:left;"> 3.</p>
                                <p style="text-align:left;"> </p>
                                <p style="text-align:left;"> &nbsp;&nbsp;&nbsp;</p><p style="text-align:left;"> 027-8713579587135349</p>
                                <p style="text-align:left;"> chenman1118@163.com</p>
                                <p style="text-align:left;"> 1</p>
                                <p style="text-align:left;"> &nbsp; &nbsp; &nbsp;2</p>
                                <p style="text-align:left;"> </p>
                                <p style="text-align:left;"> 2017918</p>
                                <p><br></p>
                                <p></p>
                            </div>
                        </el-col>
                    </el-row>
                    <!---->
                    <el-col :lg="8" :md="8" :sm="24" :xs="24">
                        <el-row :gutter="10">
                            <el-col :lg="24" :md="24" :sm="24" :xs="24">
                                <div class="news-rightlist">
                                    <div class="news-hot"></div>
                                    <div class="news-button">
                                        <button @click="week=true"></button>
                                        <button @click="week=false"></button>
                                    </div>
                                    <ul v-show="week">
                                        <li  v-for="( item,index ) in weekly" :key="index">
                                            <span>{{item.pid}}</span>{{item.description}}
                                        </li>
                                    </ul>
                                    <ul v-show="!week" style="display:none;">
                                        <li  v-for="( item,index ) in daily" :key="index">
                                            <span>{{item.pid}}</span>{{item.description}}
                                        </li>
                                    </ul>
                                </div>
                            </el-col>
                        </el-row>
                    </el-col>
                </el-row>
            </el-col>
        </el-row>
        <!--footer-->
    </div>
</template>

<script type="text/ecmascript-6">
	import api from '../axios/api.js'
	import Swiper from '../components/swiper.vue'
	export default {
		data() {
			return {
				show_read:false,
				week:true,
				limit:'10',
				category: '',//
				weekly:'',
				daily:'',
			}
		},
		created() {
			this.newApi()
		},
		methods: {
			newApi() {

				api.Get('/info/${limit}')
					.then(res => {
						this.category = res['category'];
						this.weekly = res['weekly'];
						this.daily = res['daily'];
					})
			}
		},
		components: {
			commonSwiper: Swiper
		},
	};

</script>
<style>
    /*banner*/
    .banner_img img{
        width:100%;
    }
    .el-row {
        margin-bottom: 20px;
    }

