    /**/
    .news-button > .button{
        border-radius: 4px;
        height:30px;
        line-height:30px;
        border:none;
        background-color:#ddd;
        color:#999;
        text-align: center;
        margin-right: 5px;
    }

    /**/
    .w-full {
        width: 100%;
        /*max-height:120px;*/
    }
    .no-padder a img{
        width:100%;
    }
    .h-sm {
        height: 150px;
    }
    .pos{
        height: 150px;
    }
    .text-ellipsis {
        overflow: hidden;
        display: block;
        word-break: break-all;
        white-space: nowrap;
        text-overflow: ellipsis;
        width: 100%;
    }
    .text-ellipsis {
        display: block;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .text-md {
        font-size: 16px;
    }
    .font-bold {
        font-weight: 700;
    }
    p {
        margin: 0 0 10px;
    }
    .text-dark-lt {
        color: #454b60;
    }
    a {
        color: #428bca;
        text-decoration: none;
    }
    .text-ellipsis-3 {
        height: 4.2em;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        font-size: 14px;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        line-height: 1.42857143;
        color: #333;
    }
    .pos-abt {
        position: absolute;
    }
    .pull-left {
        float: left;
    }
    .m-r {
        margin-right: 15px;
    }
    .text-muted {
        color: #98a6ad;
        font-size:14px;
    }
    .pull-left {
        float: left !important;
    }
    .m-r-xs {
        margin-right: 5px;
    }
    .pull-left {
        float: left;
    }
    .pull-left {
        float: left !important;
    }
    .b-b{
        border-bottom: 1px dashed #dee5e7;
    }
    .m-t {
        margin-top: 15px;
    }
    .m-b-lg {
        margin-bottom: 30px;
    }
    .clea {
        display: block;
        overflow: hidden;
        clear: both;
    }
    .text-center {
        text-align: center;
    }
    .text-xxxl {
        font-size: 24px;
    }
    p {
        margin: 0 0 10px;
    }
    .wrapper-lg {
        padding: 30px;
    }
    /**/
    .news-rightlist{
        border:1px solid #ddd;
        width:100%;
        /*position: absolute;*/
        padding: 0 10px  20px 20px;
        margin-bottom: 50px;
    }
    .news-hot{
        position: relative;
        top:-11px;
        left:20px;
        background-color:#fff;
        width:25%;
        text-align: center;
        color:#0089e3;
        font-weight: 600;
        font-size: 16px;
    }
    .news-button button{
        border:none;
        background-color: #ff0000;
        float:right;
        margin-right: 10px;
        border-radius: 4px;
        margin-bottom: 10px;
    }
    .news-rightlist ul li{
        clear:both;
        line-height:30px;
        color:#a1a1a1;
        font-size: 14px;
    }
    .news-rightlist ul li span{
        display: inline-block;
        width:20px;
        height:20px;
        background-color: #0089e3;
        color:#fff;
        text-align: center;
        line-height: 20px;
        margin-right: 10px;
    }
</style>


